import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/homeView.vue'
import singup from '../views/singupView.vue'
import login from '../views/loginView.vue'
import shop from '../views/shopView.vue'
import cartPay from '../views/cartPayView.vue'
import test from '../views/testView.vue'
import firstpageView from '../views/fitstpageView.vue'

const routes = [
  {
    path: '/',
    name: 'firstpage',
    component: firstpageView
  },
  {
    path: '/home',
    name: 'home',
    component: HomeView
  },
  {
    path: '/singup',
    name: 'singup',
    component: singup
  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/shop',
    name: 'shop',
    component: shop
  },
  {
    path: '/cartPay',
    name: 'cartPay',
    component: cartPay
  },
  {
    path: '/test',
    name: 'test',
    component: test
  },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
