<template>

  <cartPay  @cartPay="setPay"></cartPay>
</template>
<script>
import cartPay from './shopView.vue'

export default {
  data() {
    return {
      finalCartPay: [],
    }
  },
  components: {
    cartPay,
  },
  methods: {
    setPay(info) {
        this.finalCartPay.push(info)
 
    },
  },
}

</script>
