<template>
  <div>
    <h1>商店</h1>
    <ul class="d-flex flex-wrap container">
        <!--addCart接收到資料推入putInMyCart-->
      <Product
        @addCart="putInMyCart"
        v-for="coffeeProducts in products"
        :key="coffeeProducts.id"
        :="coffeeProducts"
      ></Product>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'
import Product from '../components/coffeeProducts.vue'

export default {
  data() {
    return {
      //推成陣列
      products: [],
      myCart: [],
    }
  },
  methods: {
      //設定一個方法將被選取到(id)的塞到陣列中
    putInMyCart(id) {
      this.myCart.push(id)
    console.log(this.myCart)

    },
  },
  components: {
    //Product:Product 將components引入。相同簡寫。
    Product,
  },
  //取得json 異步函數
  async mounted() {
    const coffee = await axios.get('http://35.201.162.58/test.php')
    this.products = coffee.data.coffee
    // console.log(coffee)
    console.log(this.products)
  },
}
</script>
