<template>
  <li class="products card col-md-3 p-3 m-5">
    <img class="w-100" src="../assets/img.jpg" alt="" />
    <div class="card-body h-100">
      <h5 card-title>品名: {{ name }}</h5>
      <p>產地: {{ origin }}</p>
      <p>焙度: {{ roast }}</p>
      <p>風味描述: {{ taste }}</p>
      <p>每半磅售價: {{ price }}</p>
    </div>
    <div class="my-2 d-flex justify-content-between">
      <button @click="reduceCount" class="btn btn-primary">-</button>
      <span>{{ count }}</span>
      <button @click="addCount" class="btn btn-primary">+</button>
    </div>
    <button @click="addCart" class="btn btn-primary">加到購物車</button>
  </li>
</template>
<script>
export default {
  data() {
    return {
      // products: [],
      //歸零
      count: 0,
    }
  },
  props: ['name', 'origin', 'roast', 'taste', 'price', 'image', 'users_id'],
  methods: {
    addCart() {
      //送出被點選到的目標/參數(物件丟資料)
      this.$emit('addCart', {
        id: this.users_id,
        count: this.count,
      })
      alert('成功加入購物車')
    },
    //TODO 如何抓到各商品的localStorage? 先點選到的push成陣列(要先確認是否在陣列內)再存入localStorage

    addCount() {
      this.count++
      this.count = this.count > 5 ? 5 : this.count
      let pdc = [this.users_id, this.count]
      localStorage.setItem("productCount", JSON.stringify(pdc));
    },
    reduceCount() {
      if (this.count > 0) {
        this.count--
        localStorage.setItem("productCount", this.count);

      }
    },
  },
}
</script>
<style src="../components/products.css">
</style>