<template>
  <div>
    <h1>商店</h1>
    <a
      @click="productFilter = 'all'"
      class="btn btn-primary m-3"
      href="javascript:;"
      >所有商品</a
    >
    <a
      @click="productFilter = 'cart'"
      class="btn btn-primary"
      href="javascript:;"
      >我的選購</a
    >

    <ul class="d-flex flex-wrap container">
      <!--addCart接收到資料推入putInMyCart-->
      <Product
        @addCart="putInMyCart"
        v-for="coffeeProducts in allProduct"
        :key="coffeeProducts.users_id"
        :="coffeeProducts"
      ></Product>
    </ul>
    <button class="btn" @click="pay" v-if="productFilter == 'cart'">結帳</button>
  </div>
</template>

<script>
import axios from 'axios'
import Product from '../components/coffeeProducts.vue'

export default {
  data() {
    return {
      //引入json堆成陣列
      products: [],
      //被選取的品項堆成陣列
      // myCart: [],
      productFilter: 'all',
    }
  },
  computed: {
    allProduct() {
      switch (this.productFilter) {
        case 'all':
          return this.products
          break
        case 'cart':
          //篩選products內的項目(item)
          let cart = this.products.filter((item) => item.count > 0)
          return cart
          break
      }
    },
  },
  methods: {
    //設定一個方法將被選取到(info)的塞到陣列中
    putInMyCart(info) {
      this.products.map(function (item) {
        if (item.users_id == info.users_id) {
          item.count = info.count
        }
      })
      // console.log(this.products);
    },
    pay() {
      let sum = 0
      this.products.forEach(function (item) {
        sum += item.price * item.count
      })
      alert(`總額為${sum}元`)
    },
    // this.myCart.push(info)
    // console.log(this.myCart)
    // this.$emit('cartPay', info)
  },
  components: {
    //Product:Product 將components引入。相同簡寫。
    Product,
  },

  //取得json 異步函數
  async mounted() {
    const coffee = await axios.get('http://35.201.162.58/test.php')
    this.products = coffee.data
    console.log(coffee)
    // console.log(this.products)
    //歸零
    this.products.forEach(function (item) {
      item.count = 0
    })
    // console.log(this.products)
  },
}
</script>
